<template>
  <div>
    <header>
      <img src="./logo-header.png" alt="logo" class="header-logo">
      <ul class="header-operations">
        <li style="cursor: default">
          <span class="header-lang is-active">尼古拉斯赵四</span>
          <span>(华北区/管理员)</span>
        </li>
        <li>退出系统</li>
      </ul></header>
  </div>
</template>
<script>
  import star from 'components/star/star.vue';
  import lines from 'components/line/line.vue';
  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data () {
      return {
        detailShow: false
      };
    },
    methods: {
      showDetail () {
        this.detailShow = true;
      },
      hideDetail () {
        this.detailShow = false;
      }
    },
    created: function () {
      this.classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee'];
    },
    components: {
      star,
      lines
    }
  };
</script>
<style>
  header {
    background-color: rgb(32, 160, 255);
    height: 60px;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    padding: 0 20px;
    z-index: 1;
    box-sizing: border-box;
  }
  .header-logo {
    display: inline-block;
    vertical-align: middle;
  }
  .header-operations {
    display: inline-block;
    float: right;
    padding-right: 30px;
    height: 100%;
  }
  .header-operations li {
    color: #fff;
    display: inline-block;
    vertical-align: middle;
    padding: 0 10px;
    margin: 0 10px;
    line-height: 60px;
    cursor: pointer;
  }
  .header-operations:after, header:after {
    display: inline-block;
    content: "";
    height: 100%;
    vertical-align: middle;
  }
</style>



